.header{
    width: 100%;
    height: 120px;
    border-bottom: 1px solid #000;
}
.header h4{
    text-align: center;
    margin: 14px 0 40px 0;
    font-size: 13px;
}
.header ul{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 62px;
}
.header ul li{
    font-size: 19px;
    font-weight: 700;
    color: #0400f3;
}
.header ul li:hover{
    color: #6b68ff;
}